@media (min-width: 1460px) {
    .container {
        position: static;
    }

    .content__section {
        padding-top: 0;
    }
}

@media (min-width: 1920px) {
    .decor img {
        max-height: 1410px;
    }
}

@media (min-width: 1920px) {
    .decor img {
        right: -40px;
    }
}

@media (max-width: 1440px) {
    .decor img {
        max-height: 1250px;
    }

    .content__title {
        font-size: 52px;
    }
}

@media (max-width: 1200px) {
    .decor img {
        max-height: inherit;
        max-width: 900px;
    }
}

@media (max-width: 1024px) {
    .header {
        position: relative;
        padding: 48px 0;
    }

    .content__section {
        padding-top: 0;
        max-width: 44%;
        display: block;
    }

    .content__title {
        font-size: 32px;

        span {
            font-size: 46px;
        }
    }

    .content {
        display: block;
        height: auto;
        padding-top: 16px;
    }

    .decor img {
        margin-top: 208px;
    }
}

@media (max-width: 980px) {
    .content__title {
        max-width: 540px;
        margin-bottom: 16px;
    }

    .content__section {
        max-width: 100%;
    }

    .content__subtitle {
        font-size: 20px;
        max-width: 650px;
    }
}

@media (max-width: 767px) {
    .container {
        height: auto;
        padding: 0 25px;
    }

    .header {
        padding: 24px 0;
    }

    .socials-list li + li {
        margin-left: 16px;
    }

    .header__logo {
        max-width: 100px;
    }

    .content__section {
        max-width: 100%;
    }

    .content__subtitle {
        font-size: 16px;
    }

    .decor img {
        max-height: 320px;
        max-width: inherit;
        top: inherit;
        bottom: -160px;
        right: 0;
    }

    #circle {
        display: none;
    }
}

@media (max-width: 480px) {
    .content__title {
        span {
            display: block;
        }
    }
}
